          titleUrl: require("../assets/prod/prodtwo/titletwo.png"),
          phoneUrl: require("../assets/prod/prodtwo/phonetwo.png"),
          tips:
            "与客户对发型设计进行深度交流，更好的了解客户需求，提供各种营销工具，提升客户满意度和到店率。"
        },
        {
