        }
      ]
    };
  },
  components: {
    headerBar,
    footerBar
  },
  methods: {
    goright() {
      let self = this;
      if (this.active < 2) {
        this.activeCircle += 1;
        let active = this.active;
        this.active = "占位";
        var clock = setTimeout(function() {
          active += 1;
          self.active = active;
          self.content = {
            ...self.list[active]
          };
        }, 500);
      }
    },
    goleft() {
      let self = this;
      if (this.active > 0) {
        this.activeCircle -= 1;
        let active = this.active;
        this.active = "占位";
        var clock = setTimeout(function() {
          active -= 1;
          self.active = active;
          self.content = {
            ...self.list[active]
          };
        }, 500);

        // this.active -= 1;
        // this.content = {
        //   ...this.list[this.active]
        // };
      }
    }
  }
};
